.bsw-body-login .ant-spin-container {
  width: 100%;
  min-height: 100%;
  padding: 120px 0;
  transition: padding 0.5s ease-in-out;
}
@media screen and (max-width: 576px) {
  .bsw-body-login .ant-spin-container {
    padding: 50px 0;
  }
}
@media screen and (min-height: 1000px) {
  .bsw-body-login .ant-spin-container {
    padding: 200px 0;
  }
}
.bsw-body-login .header {
  width: 100%;
  text-align: center;
  user-select: none;
}
.bsw-body-login .header a {
  text-decoration: none;
}
.bsw-body-login .header img {
  width: 50px;
  vertical-align: top;
  margin-right: 10px;
}
.bsw-body-login .header span {
  color: #1890ff;
  font-size: 32px;
  padding-right: 14px;
  font-weight: 200;
}
.bsw-body-login .login-container {
  width: 460px;
  margin: 0 auto;
  padding: 40px 0;
  background: rgba(255, 255, 255, 0.3);
  border: 1px solid #ffffff;
  border-radius: 4px;
}
.bsw-body-login .login-container.dark {
  background: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(6, 6, 6, 0.2);
}
@media screen and (max-width: 576px) {
  .bsw-body-login .login-container {
    width: unset;
    background: none;
    border: none;
  }
}
.bsw-body-login .login-form {
  margin: 64px auto 0;
  max-width: 360px;
  transition: max-width 0.5s ease-in-out;
}
.bsw-body-login .login-form .login-form-forgot {
  float: right;
}
.bsw-body-login .login-form .login-form-button {
  width: 100%;
}
.bsw-body-login .login-form .ant-input-prefix .anticon {
  color: rgba(0, 0, 0, 0.25);
}
.bsw-body-login .login-form .ant-input-affix-wrapper .ant-input:not(:first-child) {
  padding-left: 36px;
}
@media screen and (max-width: 576px) {
  .bsw-body-login .login-form {
    margin: 70px auto 0;
    max-width: 320px;
  }
}
.bsw-body-login .bsw-captcha-item .ant-form-item-children {
  display: flex;
  flex-direction: row;
}
.bsw-body-login .bsw-captcha-item .ant-form-item-children > span {
  flex: 1;
}
.bsw-body-login .bsw-captcha-item .ant-form-item-children > img.bsw-captcha {
  height: 40px;
  margin-left: 10px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
}
